import {
  IUserServiceLayerView,
  IUserServiceLayerController,
} from "../../contracts/Portal_Configuration/UserServiceLayerContract";

import axios from "../../plugins/axios";

export class UserServiceLayerController implements IUserServiceLayerController {
  _view: IUserServiceLayerView;

  constructor(view: IUserServiceLayerView) {
    this._view = view;
  }

  getUser(): void {
    axios
      .get("UserServiceLayer")
      .then((Response: any) => {
        if (Response.data && Response.data.pass) {
          this._view.updateUser(
            Response.data.user,
            atob(Response.data.pass),
            Response.data.companyDB
          );
        } else {
          this._view.updateUser("", "", "");
        }
      })
      .catch((e: any) => {
        this._view.showMsg("Náo foi possível carregar as informações.", 1);
        console.log(e);
      });
  }

  salvar(user: String, pass: String, companyDB: String): void {
    axios
      .post("UserServiceLayer", {
        User: user,
        Pass: pass,
        CompanyDB: companyDB,
      })
      .then((Response: any) => {
        console.log(Response);
        if (Response.data.sucesso) {
          this._view.showMsg("Salvo com sucesso.", 1);
        } else {
          this._view.showMsg(Response.data.message, 0);
        }
      })
      .catch((e: any) => {
        this._view.showMsg("Náo foi possível carregar as informações.", 1);
        console.log(e);
      });
  }
}
