

















import Vue from "vue";
import { Headers } from "../models/headers";

export default Vue.extend({
  props: {
    headers: {
      type: Headers
    },
    itens: {
      type: Array,
      default: []
    },
    search: String,
    defaultItens: {
      type: Boolean,
      default: true
    },
    defaultHeaders: {
      type: Boolean,
      default: true
    },
    defaultFooter: {
      type: Boolean,
      default: true
    }
  }
});
