

























































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import { Display } from "../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

import {
  IUserServiceLayerView,
  IUserServiceLayerController
} from "../../contracts/Portal_Configuration/UserServiceLayerContract";

import { UserServiceLayerController } from "../../controllers/Portal_Configuration/UserServiceLayerController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class UserServiceLayer extends Vue
  implements IUserServiceLayerView {
  _controller?: IUserServiceLayerController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  visiblePass = false;

  sairMessage = false;

  serviceLayer = {
    user: "",
    pass: "",
    companyDB: ""
  };

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string, modo: number): void {
    this.textError = msg;
    this.dialogMessage = true;
    this.sairMessage = modo == 1;
  }

  fecharDialogMessage() {
    this.dialogMessage = false;
    if (this.sairMessage) {
      this.$router.push("/");
    }
  }

  isMobile = false;

  save() {
    this._controller!!.salvar(
      this.serviceLayer.user,
      this.serviceLayer.pass,
      this.serviceLayer.companyDB
    );
  }

  updateUser(user: string, pass: string, companyDB: string): void {
    this.serviceLayer.user = user;
    this.serviceLayer.pass = pass;
    this.serviceLayer.companyDB = companyDB;
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new UserServiceLayerController(this);

    this._controller!!.getUser();
  }
}
